import Toolbar from "./toolbar"
import Column from "./column"
import ScrollBar from "./scrollbar"

const App = (mdl) => {
  return {
    view: () =>
      m(".app", [
        m(Toolbar, { mdl }),
        m(
          ".board.container.columns col-oneline",
          mdl.cols.map((col) => m(Column, { col, mdl }))
        ),
        m(ScrollBar, { mdl }),
      ]),
  }
}

export default App
